<template>
	<div class="app-content" v-if="!!Object.keys(content).length">
		<div class="container">
			<div class="page-title">
				<h1>Вопросы и ответы</h1>
				<div class="actions">
					<button class="btn btn-success" @click="savePage()" :disabled="saveInProgress">
						<span class="fas fa-save"></span>
						Сохранить
					</button>
				</div>
			</div>
			<div class="page-content">
				<div class="progress" v-if="saveInProgress">
					<div class="progress-bar progress-bar-striped progress-bar-animated" role="progressbar" style="width: 100%"></div>
				</div>
				<div class="alert alert-success" v-if="isSavedSuccess" role="alert">
					Изменения успешно сохранены
				</div>
				<fieldset>
					<div class="row">
                        <div class="col-12">
                            <label class="form-label">Заголовок страницы</label>
                            <InputLangGroup v-model="content.pageTitle" :content="content.pageTitle"/>
                        </div>
					</div>
				</fieldset>
                <fieldset v-for="(question, i) in content.list" :key="i">
                    <legend>Вопрос {{ i + 1 }}</legend>
					<div class="row">
                        <div class="col-12">
                            <label class="form-label">Название</label>
                            <InputLangGroup v-model="content.list[i].title" :content="content.list[i].title"/>
                        </div>
                        <div class="col-12">
                            <label class="form-label">Текст</label>
                            <InputLangGroup v-model="content.list[i].text" :content="content.list[i].text" :isTextarea="true"/>
                        </div>
					</div>
				</fieldset>
                <button class="btn btn-primary" @click="addQuestion()">
                    <span class="fas fa-plus"></span>
                    <span>Добавить</span>
                </button>
			</div>
		</div>
	</div>
</template>

<script>
import InputLangGroup from '../components/InputLangGroup.vue';

export default {
    data() {
        return {
            content: '',
            saveInProgress: false,
            isSavedSuccess: false
        }
    },
    components: {
        InputLangGroup
    },
    computed: {
        pageContent() {
            return this.$store.getters['getFAQpageContent'];
        }
    },
    watch: {
        pageContent(value) {
            this.content = value;
        }
    },
    methods: {
        savePage() {
            this.saveInProgress = true;
            this.$store.dispatch('saveFAQPage', this.content)
                .then(() => {
                    this.saveInProgress = false;
                    this.isSavedSuccess = true;
                    setTimeout(() => {
                        this.isSavedSuccess = false;
                    }, 3000);
                });
        },
        addQuestion() {
            this.content.list.push({
                title: {
                    ua: '',
                    ru: '',
                    en: ''
                }, 
                text: {
                    ua: '',
                    ru: '',
                    en: ''
                }
            });
        }
    },
    created() {
        this.content = this.pageContent;
    },
    mounted() {

    }
};
</script>